@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/utilities/mixins' as *;
@use 'sass:color';

.session-list-wrapper {
  label {
    width: 100%;
    margin-bottom: 6px;

    .session-card {
      padding: 10px 16px;
      border: 1px solid color.adjust(pick-color('primary', 'web'), $lightness: 25%);
      display: grid;
      grid-template-columns: 220px 220px 40px;
      grid-template-rows: 1fr;
      gap: 16px;
      margin-left: 0;
      width: auto;

      span {
        font-weight: 500;
        color: pick-color('primary', 'charcoal');
        line-height: 18px;
        font-size: 14px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }

      .dropdown-toggle-wrapper {
        justify-self: center;

        span {
          font-weight: 400;
        }

        .dropdown-toggle-btn {
          @include circles(32px, color.adjust(pick-color('primary', 'web'), $lightness: 15%));
          border-radius: 4px !important;
          cursor: pointer;
          transition: all 0.2s ease;

          .toggle-icon {
            width: 20px;
            height: 20px;
            font-size: 20px;
            transition: all 0.2s ease;
            color: white;
          }

          &.toggled {
            background-color: pick-color('primary', 'web');

            .toggle-icon {
              transform: rotate(-180deg);
            }
          }
        }
      }
    }

    .dropdown-content-wrapper {
      .session-card-file {
        @extend .session-card;
        grid-template-columns: 1fr 1fr;
        padding-right: 26px;
        border: none;
        margin-top: 8px;
        position: relative;
        background-color: color.adjust(pick-color('primary', 'blue'), $lightness: 50%);

        &::before {
          content: '';
          position: absolute;
          height: 100%;
          border-left: 4px solid pick-color('primary', 'web');
          width: 20px;
          border-radius: inherit;
        }

        &.selected-file {
          background-color: color.adjust(pick-color('primary', 'blue'), $lightness: 40%);
        }

        .file-type-icon {
          @include circles(28px, pick-color('primary', 'web'));
          flex-shrink: 0;
        }

        span {
          color: pick-color('primary', 'web');

          &.filename {
            cursor: pointer;
          }
        }

        &.file-not-exists,
        &.file-not-exists.file-type-icon {
          background-color: pick-color('primary', 'fog');
          cursor: not-allowed;

          .file-type-icon {
            background-color: pick-color('primary', 'ash');
          }

          span {
            color: pick-color('primary', 'ash');
            cursor: inherit;
          }
        }

        &.redaction-progress::before {
          border-left: 4px solid pick-color('common', 'warning');
        }

        &.redaction-progress,
        &.redaction-progress.file-type-icon {
          background-color: color.adjust(pick-color('common', 'warning'), $lightness: 30%);
          cursor: not-allowed;

          .file-type-icon {
            background-color: pick-color('common', 'warning');
          }

          span {
            color: pick-color('common', 'warning');
            cursor: inherit;
          }
        }
      }
    }
  }
}
